.button {
  border: var(--button-border);
  border-radius: var(--spacing-xxs);
  color: var(--utility-neutral-0);
  font-size: var(--button-font-size);
  font-weight: var(--button-font-weight);
  margin: var(--button-margin);
  padding: var(--button-padding-small);
  align-items: center;
  display: inline-flex;
  justify-content: center;
  line-height: 1;
  padding: 4px;
  vertical-align: middle;
  color: var(--theme-a-4);
}
.button.red {
  background-color: var(--utility-red-50);
}
.button.red:hover {
  background-color: var(--utility-red-70);
}
.button.green {
  background-color: var(--utility-green-60);
}
.button.green:hover {
  background-color: var(--utility-green-80);
}
.button.blue {
  background-color: var(--theme-a-4);
}
.button.blue:hover {
  background-color: var(--theme-a-3);
}
.button:focus-visible {
  outline: 2.5px solid var(--theme-a-3);
  outline-offset: var(--outline-offset-s);
}
.button.disabled {
  background-color: var(--utility-neutral-10);
  color: var(--utility-neutral-60);
}
.button.disabled:hover {
  background-color: var(--utility-neutral-10);
  color: var(--utility-neutral-60);
}
.button .iconStart {
  margin-right: var(--spacing-xs);
}
.button .iconEnd {
  margin-left: var(--spacing-xs);
}
.button .iconStart,
.button .iconEnd {
  align-items: center;
  display: flex;
}
.button.red, .button.green, .button.blue, .button.disabled {
  background: none;
}
.button.red:hover, .button.green:hover, .button.blue:hover, .button.disabled:hover {
  background: none;
}
.button.red {
  color: var(--utility-red-50);
}
.button.red:hover {
  color: var(--utility-red-70);
}
.button.green {
  color: var(--utility-green-70);
}
.button.green:hover {
  color: var(--utility-green-80);
}
.button.blue {
  color: var(--theme-a-4);
}
.button.blue:hover {
  color: var(--theme-a-3);
}
.button.disabled {
  color: var(--utility-neutral-60);
}